<template>
  <div class="home">

       <div class="tittle">
      <van-nav-bar>
        <template #right>
          <h2>{{timeText}}</h2>
          <span>{{nickName}}</span>
        </template>
         <template #left>
          <div class="img">
            <img src="../../assets/images/icon_luckin_coffee.png" class="auto-img" alt="">
            </div>
        </template>
      </van-nav-bar>
     
    </div>
    <div class="search">
      <van-search v-model="value" shape="round" background="#0C34BA" placeholder="请输入搜索关键词" @input="searchShop(value)"/>
      <ul v-if="isshow">
        <li class="search-text" v-for="(item,index) in searchData" :key="index" @click="viewProductInfo(item.pid)">{{item.name}}</li>
      </ul>
    </div>

    <div class="swipe">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bgImg" :key="index">
          <img :src="item.bannerImg" alt class="auto-img" @click="viewProductInfo(item.pid)" />
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="recommend">
        <h1>热门推荐</h1>
      <van-tabs>
        <van-tab v-for="(item,index) in shopData" :title="'周' + (index+1)"  :key="index">
            <div class='p-img' @click="viewProductInfo(item.pid)">
                <img :src="item.smallImg" alt="" class="auto-img">
                <div class="p-text">{{item.name}}</div>
            </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      timeText: "",
      bgImg: [],
      shopData:[],
      searchData:[],
      nickName:'',
      isshow: false
    };
  },
  created() {
    this.getTime();
    //获取轮播图片
    this.getSwipeImg();
    //获取推荐商品
    this.getRecommendShop();
     //查询用户信息
    this.getUserInfo()
  },

  methods: {
    //根据时间改变
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好！";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好！";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好！";
      } else {
        this.timeText = "深夜好！";
      }
    },

    //获取轮播图片
    getSwipeImg() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });
      this.axios({
        method: "GET",
        //请求参数
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
        //   
          this.$toast.clear();
          if (result.data.code == 300) {
            this.bgImg = result.data.result;
          }
        })
        .catch(err => {
          
        });
    },

    //获取推荐商品
    getRecommendShop() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });
      this.axios({
        method: "GET",
        //请求参数
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: 'isHot',
          value: 1
        }
      })
        .then(result => {
          
          this.$toast.clear();
          if (result.data.code == 500) {
            this.shopData = result.data.result;
          }
        })
        .catch(err => {
          
        });
    },

    //搜索商品
    searchShop(value){
      
      if (value == '') {
        this.isshow = false;
        return
      };
      this.isshow = true;
      let params = {
        appkey:this.appkey,
        name:this.value
      }
      this.axios({
       method:'GET',
       url:'search',
       params
      }).then(result =>{
        
        this.searchData = result.data.result;
        
      })
    },
    
    //查看商品详情
    viewProductInfo(pid){
      this.$router.push({name:'Detail',query:{pid}});
    },

    //查询用户信息
    getUserInfo(){
                
                let tokenString = localStorage.getItem('CSTK');
                if (!tokenString) {
                    return;
                }

               this.$toast.loading({
                  message: '加载中...',
                  forbidClick: true,
                  duration: 0,
                  loadingType: 'spinner'
                })

                this.axios({
                    method: 'GET',
                    url: '/findMy',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then(result =>{
                    this.$toast.clear();
                    
                    if (result.data.code == 'A001') {
                        this.nickName = result.data.result[0].nickName;
                        if (result.data.result.length > 0) {
                            this.isLogin = true;
                            this.userInfo = result.data.result[0];
                        }
                    }
                }).catch(err =>{
                    this.$toast.clear();
                    
                })
    },
  }
};
</script>

<style lang="less" scoped>
.tittle {
  span {
    font-size: 15px;
    line-height: 30px;
    height: 30px;
    font-weight: bold;
  }
  .van-hairline--bottom::after {
    border: none;
  }
  .text {
    text-align: left;
    font-size: 16px;
    padding-left: 20px;
    font-weight: bold;
  }
}
.recommend{
   
    margin-top: 30px;
    margin-bottom: 49.98px;
     /deep/ .van-tab.van-tab--active{
        span{
            width: 30px;
            height: 30px;
             border-radius: 50%;
              background-color: #0C34BA;
              line-height: 30px;
              color: white;
              
        }
    }
      /deep/ .van-tabs__line{
         display: none
      }
  .p-img{
      margin: 20px;
       position: relative;
     img{
         border-radius: 20px;
     }

      .p-text{
      position: absolute;
      padding:5px 10px;
      background-color: #0C34BA;
      border-top-left-radius: 20px;
      border-bottom-left-radius:20px;
      color: white; 
      top: 30px;
      right: 0;
  }
  }
 
}
.img{
  width: 45px;
  height: 45px;
}
.search{
  ul{
    text-align: left;
    
    .search-text{
      margin: 5px 20px;
      padding: 5px 20px;
      background-color: #f5f5f5;
      border-radius: 20px;
    }
  }
}
</style>